import ReactToolTip from 'react-tooltip';

function ProtApp(props) {
    const splitTech = tech => {
        const arr = [];
        for(const item of tech) {
            arr.push(<li className="p-0 m-1">{item}</li>)
        }
        return arr;
    }
    return (
        <div>
            <li data-tip data-for={`url${props.protIDNum}`}><a target="_blank" rel="noreferrer" href={props.href} className={`btn webBtn w-100 text-left`}>{props.globeIcon()}{props.title}.</a></li>
            <ReactToolTip id={`url${props.protIDNum}`}>
                <div>
                    <h5>Contains:</h5>
                    <ul className="p-0 m-1">
                        {splitTech(props.tech)}
                    </ul>
                    <img alt="Preview" src={props.picture} style={{width: '300px'}}/>
                </div>
            </ReactToolTip>
        </div>
    )
}

export default ProtApp;